<template>
    <v-container>
        <v-row class="ma-0 pa-0">
            <v-col cols="auto" id="leftMenu" style="display:block;margin-top: 0px;">    
                <template class="ma-0 pa-0">
                    <v-card class="mx-auto" width="300">
                        <v-list>
                            <v-list-group v-bind:value="leftMenuGroup.seller" prepend-icon="mdi-account-circle">
                                <template v-slot:activator>
                                    <v-list-item-title align="start">Payment</v-list-item-title>
                                </template>
                                <v-list-item id="report-payment-list" @click="leftMenuClick('report-payment-list')">
                                    <v-list-item-title style="margin-left:60px;" align="start">List</v-list-item-title>
                                </v-list-item>
                            </v-list-group>
                        </v-list>
                        <v-list-item link @click="leftMenu('hide')">
                            <v-list-item-icon><v-icon>mdi-swap-horizontal</v-icon></v-list-item-icon>
                            <v-list-item-title align="start">Hidden</v-list-item-title>
                        </v-list-item>
                    </v-card>
                </template>
            </v-col>
            <div><v-list-item-icon @click="leftMenu('show')" style="cursor:pointer;display:none" id="leftMenuHide"><v-icon>mdi-swap-horizontal</v-icon></v-list-item-icon></div>  
            
            <v-col>
                
                <v-row>
                    <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <vue-country-code style="display:none" />
                    </v-col>
                    <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <v-menu v-model="fromDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field v-model="fromDate" label="From" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
                            </template>
                            <v-date-picker v-model="fromDate" @input="fromDateMenu = false"></v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <v-menu v-model="toDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field v-model="toDate" label="To" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
                            </template>
                            <v-date-picker v-model="toDate" @input="toDateMenu = false"></v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <v-text-field hide-details label="Search" placeholder="Search" dense outlined v-model="searchText" @keyup="keyEnter" ref="search" style="width: 270px;top:10px"></v-text-field>
                    </v-col>
                    <v-col align="left" class="ma-0 pa-0 flex-grow-0 flex-shrink-0">
                        <v-tooltip top color="#18A2D9">
                            <template v-slot:activator="{ on, attrs }">
                                <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:10px" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/magnify.png" width="40px" @click="getDataInit()" />
                            </template>
                            <span>Search</span>
                        </v-tooltip>
                    </v-col>

                    <v-col class="ma-1 pa-1 flex-grow-0 flex-shrink-0"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </v-col>
                    <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                        <v-tooltip top color="#18A2D9">
                            <template v-slot:activator="{ on, attrs }">
                                <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/download-file_toolbar.png" width="40px" @click="getMakeExcel()"/>
                            </template>
                            <span>Download Excel</span>
                        </v-tooltip>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col align="left" style="padding-top:10px;padding-bottom:0px;">
                        Total : {{ desserts.length }} / Selected : {{ selected.length }}
                    </v-col>
                </v-row>
                
                <v-row>
                    <v-col>
                        <v-data-table :headers="headers" :items="desserts" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
                            <template v-slot:item="props">
                                <tr style="height:40px;">
                                    <td style="height:30px;" align="center">
                                        <input type="checkbox" style="width:20px;height:20px" @change="setCheck(props.item, $event.target.checked)" name="item">
                                    </td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="start">{{ props.item.createdAt}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="start">{{ props.item.paymentNo}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="start">{{ props.item.paymentName}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="right">₩ {{ props.item.paymentAmount | comma}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="right">₩ {{ props.item.cancelAmount | comma}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="right">₩ {{ props.item.finalAmount | comma}}</td>
                                    <td style="height:30px;cursor:pointer" @click="getPaymentDetail(props.item)" align="center">{{ props.item.status}}</td>
                                </tr>
                            </template>
                        </v-data-table>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
        <!-- paymentDetail 다이얼로그 -->
        <v-dialog v-model="paymentDetailDialogShow" persistent max-width="1200px">
            <template v-slot:activator="{ on, attrs }">
                <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
            </template>
            <v-card>
                <v-card-text>
                    <v-card-title class="ma-0 pa-0">
                        <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;"><b>Payment Detail</b></span>
                    </v-card-title>
                <v-row>
                    <v-col class="ma-0 pa-0">
                        <table border="0" style="width:100%;border-collapse : collapse">
                            <tr style="background-color: #1BB5F2;height:50px;">
                                <th align="start" style="padding-left:20px;width:170px">Transaction Date</th>
                                <th align="start" style="padding-left:20px;width:200px">GEM Master #</th>
                                <th align="center" style="width:200px">Consignee</th>
                                <th align="start" style="padding-left:20px;">Item</th>
                                <th align="center" style="width:150px">GW / VW</th>
                                <th align="center" style="">Paid</th>
                            </tr>
                            <tr v-for="item in paymentDetail.orders" :key="item.id" style="height:50px;">
                                <td align="start" style="padding-left:20px;">{{ item.createdAt }}</td>
                                <td align="start" style="padding-left:20px;">{{ item.gemMasterNo }}</td>
                                <td align="center"><div style="width:200px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ item.recipientName }}</div><div style="margin:1px" :class="getCountry(item.recipientAddressCountryCode)"></div>&nbsp;({{item.recipientAddressCountryCode}})</td>

                                <td align="start">
                                    <div style="width:300px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ item.mainItemQuantity }} X {{ item.mainItemName }}<br />
                                    Qty: {{ item.totalItemQuantity }}, Value: {{ item.mainItemCurrency | currency }} {{ item.totalItemAmount | comma }}
                                    </div>
                                </td>
                                <td align="center">
                                    {{ item.shipmentActualWeight }} <span v-if="item.weightUnit == null || item.weightUnit == ''">kg</span><span v-else>{{ item.weightUnit }}</span> 
                                    / {{ item.shipmentVolumeWeight }} <span v-if="item.weightUnit == null || item.weightUnit == ''">kg</span><span v-else>{{ item.weightUnit }}</span> 
                                </td>
                                <td align="right" style="padding-right:10px">₩ {{ item.paymentAmount | comma }}</td>
                            </tr>
                        </table>
                        <table v-show="paymentDetail.status == 'PARTIAL_CANCEL' || paymentDetail.status == 'CANCEL'" border="0" style="width:100%;border-collapse : collapse;margin-top: 20px;">
                            <tr style="background-color: #1BB5F2;height:50px;">
                                <th align="start" style="padding-left:20px;width:180px">Cancellation Date</th>
                                <th align="start" style="padding-left:20px;width:200px">GEM Master #</th>
                                <th align="start" style="padding-left:20px;width:500px">Reason</th>
                                <th align="right" style="padding-right:10px">Cancelled</th>
                            </tr>
                            <tr v-for="item in paymentDetail.cancels" :key="item.id" style="height:50px">
                                <td align="start" style="padding-left:20px;">{{ item.canceledAt }}</td>
                                <td align="start" style="padding-left:20px;">{{ item.gemMasterNo }}</td>
                                <td align="start" style="padding-left:20px;">{{ item.reason }}</td>                                
                                <td align="right" style="padding-right:10px">₩ {{ item.amount | comma }}</td>
                            </tr>
                        </table>
                    </v-col>
                </v-row>
                </v-card-text>            
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="paymentDetailOk()"> Ok </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- paymentDetail 다이얼로그 -->        
        <!-- 로딩바 -->
        <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
        <!-- 로딩바 -->  
        </v-container>
</template>

<style scoped>
.bg-active {
  background-color: #0aaae9;
  color : white !important;
}
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #9BC298;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 2;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.headerCss {
  background-color: darksalmon;
  font-size: 20px;
  background-color: red;
}
.v-text-field {
  width: 250px;
}

.v-data-table > .v-data-table__wrapper > table > tbody > tr > td,
.v-data-table > .v-data-table__wrapper > table > tfoot > tr > td,
.v-data-table > .v-data-table__wrapper > table > thead > tr > td {
  font-size: 12px;
}
</style>
<script>
import VueCookies from "vue-cookies";
import $ from "jquery";
import axios from "axios";
import common from '@/common'
import dateUtil from "@/common/DateUtil"
import loadingBarView from "@/components/LoadingBarView.vue"

export default {
    components: {
        loadingBarView
    },
    data() {
        return {
            paymentDetailDialogShow: false,
            setLoadingBar : { loadingBarDialogShow : false },
            leftMenuGroup : { seller : true, order : false},
            fromDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
            fromDateMenu: false,
            toDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
            toDateMenu: false,
            searchText: "",
            paymentDetail: "",
            selectedTasks: [],
            selected: [],
            desserts: [],
            headers: [
                { text: "Transaction Date", value: "createdAt", width: "180" },
                { text: "Payment Key", value: "paymentNo" },
                { text: "Description", value: "paymentName" },
                { text: "Paid", value: "paymentAmount", align: "right" },
                { text: "Cancelled", value: "cancelAmount", align: "right" },
                { text: "Total", value: "finalAmount", align: "right" },
                { text: "Status", value: "status", align: "center" },
            ],
        }
    },
    filters: {       
    },
    computed: {
    },
    methods: {
        getDataInit() {
            this.setLoadingBar.loadingBarDialogShow = true;
            axios
            .get(common.makeAPIUrl('/payments?userType=SELLER&sellerId=' + this.$session.get('loginId') + '&startDate=' + this.fromDate + '&endDate=' + this.toDate + '&keyword=' + this.searchText + '')
            , { headers: {'Authorization': this.$session.get('authorization')}})
            .then(response => {
                this.desserts = response.data;
                for(var paymentCount = 0; paymentCount < this.desserts.length; paymentCount++)
                {
                    this.desserts[paymentCount].createdAt = common.getYYYMMDD(this.desserts[paymentCount].createdAt)
                }
                this.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert(common.messages.login, "", "info").then(() => { this.$router.push("/"); });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.setLoadingBar.loadingBarDialogShow = false;
            })
        },
        getCountry(value) {
            return common.getCountry(value)
        },        
        paymentDetailOk() {
            this.paymentDetailDialogShow = false;
        },
        leftMenu(display) {
            $("#leftMenu").hide();
            $("#leftMenuHide").hide();

            if(display == "show") { $("#leftMenu").show(); }
            if(display == "hide") { $("#leftMenuHide").show(); }
        },        
        getPaymentDetail(item) {
            var vm = this;
            vm.setLoadingBar.loadingBarDialogShow = true;

            axios
            .get(common.makeAPIUrl("/payments/" + item.id + "/detail"), {
                headers: { "Content-Type": "application/json", Authorization: this.$session.get("authorization") },
            })
            .then((response) => {
                vm.paymentDetail = response.data;

                for(var orderCount = 0; orderCount < vm.paymentDetail.orders.length; orderCount++) {
                    vm.paymentDetail.orders[orderCount].createdAt = common.getYYYMMDD(vm.paymentDetail.orders[orderCount].createdAt);
                }
                for(var cancelCount = 0; cancelCount < response.data.cancels.length; cancelCount++){
                    vm.paymentDetail.cancels[cancelCount].canceledAt = common.getYYYMMDD(vm.paymentDetail.cancels[cancelCount].canceledAt);
                }
                vm.paymentDetailDialogShow = true;
                vm.setLoadingBar.loadingBarDialogShow = false;
            })
            .catch((err) => {
                vm.setLoadingBar.loadingBarDialogShow = false;
                vm.$alert(err.response.data.message, "", "info").then(() => {});
            });            
        },
        setAllCheck(e) {
            var checked = e.target.checked;
            this.selected = [];
            $('input[name="item"]').prop('checked', checked)
                if(checked) {
                    for(var count=0; count < this.desserts.length; count++) {
                        this.selected.push(this.desserts[count]);
                    }
                }
        },
        setCheck(item, checked) {
            if(checked) {
                this.selected.push(item);
            } else {
                const idx = this.selected.indexOf(item)
                if (idx > -1) { this.selected.splice(idx, 1) }
            }
        },        
        keyEnter(value) {
            if (value.code == "Enter") {
                this.getDataInit();
            }
        },
        getMakeExcel() {
            this.selectedTasks = this.selected;
            var vm = this;
            if(this.selectedTasks != "") {
                vm.setLoadingBar.loadingBarDialogShow = true;
                let parmas = [];
                for(let paymentCount = 0; paymentCount < this.selectedTasks.length; paymentCount++) {
                    parmas.push(this.selectedTasks[paymentCount].id);
                }

                axios
                .get(common.makeAPIUrl("/payments/download?paymentIds=" + parmas), {
                    headers: { "Content-Type": "application/json", Authorization: this.$session.get("authorization") },
                })
                .then((response) => {
                    var excelDataList = [];

                    excelDataList.push(["Transaction Date", "Payment Key", "Seller Code", "Seller", "Description", "Transaction Paid", "Transaction Cancelled", "Transaction Total", "Gem Master #", "Consignee", "Item", "GW", "VW", "Paid", "Status", "Cancelled", "Cancellation Date", "Reason"]);

                    for(var paymentCount=0;paymentCount<response.data.length;paymentCount++) {
                        var TransactionDate = common.getYYYMMDD(response.data[paymentCount].createdAt);
                        var PaymentKey = response.data[paymentCount].paymentId;
                        var SellerCode = response.data[paymentCount].sellerCode;
                        var Seller = response.data[paymentCount].sellerCompanyName;
                        var Description = response.data[paymentCount].paymentName;
                        var Paid = response.data[paymentCount].paymentAmount;
                        var Cancelled = response.data[paymentCount].cancelAmount;
                        var Total = response.data[paymentCount].finalAmount;
                        var Status = response.data[paymentCount].status;
                        
                           
                        var GemMasterNo = response.data[paymentCount].order.gemMasterNo;
                        var Consignee = response.data[paymentCount].order.recipientName + " (" + response.data[paymentCount].order.recipientAddressCountryCode + ")";
                        var ItemQty = response.data[paymentCount].order.totalItemQuantity;
                        var Item = response.data[paymentCount].order.mainItemQuantity + " X " + response.data[paymentCount].order.mainItemName + " Qty: " + ItemQty + ", Value: " + response.data[paymentCount].order.mainItemCurrency + " " + response.data[paymentCount].order.totalItemAmount;
                        
                        var GW = "";
                        if(response.data[paymentCount].order.weightUnit == null || response.data[paymentCount].order.weightUnit == "") {
                            GW = response.data[paymentCount].order.shipmentActualWeight + " kg";
                        } else {
                            GW = response.data[paymentCount].order.shipmentActualWeight + " " + response.data[paymentCount].order.weightUnit;
                        }

                        var VW = "";
                        if(response.data[paymentCount].order.weightUnit == null || response.data[paymentCount].order.weightUnit == "") {
                            VW = response.data[paymentCount].order.shipmentVolumeWeight + " kg";
                        } else {
                            VW = response.data[paymentCount].order.shipmentVolumeWeight + " " + response.data[paymentCount].order.weightUnit;
                        }
                        
                        var OrderPaid = response.data[paymentCount].order.paymentAmount;
                        var CancellationDate = "";
                        var Reason = "";
                        var DetailCancelAmount = "";

                        if(response.data[paymentCount].order.cancelAmount != 0) {
                            CancellationDate = common.getYYYMMDD(response.data[paymentCount].order.canceledAt);
                            Reason = response.data[paymentCount].order.cancelReason;
                            DetailCancelAmount = response.data[paymentCount].order.cancelAmount;
                        }
                        
                        excelDataList.push([
                            TransactionDate, PaymentKey, SellerCode, Seller,Description, Paid, Cancelled, Total, GemMasterNo, Consignee, Item, GW, VW, OrderPaid, Status, DetailCancelAmount, CancellationDate, Reason
                        ]);      
                    }

                    const xlsx = require('xlsx');
                    const workBook = xlsx.utils.book_new()
                    const workSheet = xlsx.utils.aoa_to_sheet(excelDataList)
                    xlsx.utils.book_append_sheet(workBook, workSheet, 'GEM_Excel')

                    xlsx.writeFile(workBook, 'GEM_' +  VueCookies.get("companyName") + '_PAYMENT_LIST' + "_" + dateUtil.nowYYYYmmdd() + ".xlsx")
                    
                    vm.setLoadingBar.loadingBarDialogShow = false;
                })
                .catch((err) => {
                    vm.setLoadingBar.loadingBarDialogShow = false;
                    vm.$alert(err.response.data.message, "", "info").then(() => {});
                });
            } else {
                this.$alert(common.messages.paymentSelect, '', 'info').then(() => { vm.setLoadingBar.loadingBarDialogShow = false; })   
            }
        },
    },
    mounted() {
        if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
            this.$alert(common.messages.login, "", "info").then(() => {
                this.$router.push("/");
            });
            return false;
        }
              
        $(document).prop("title", "Report Management");
        $("#bar_header").attr("style", "background-color:#E2E1F1");
        $("#top_header").show();
        $("#topNaviText").text("Report Management");

        this.fromDate = dateUtil.fromYYYYmmdd();
        this.toDate = dateUtil.toYYYYmmdd();

        var dataTableHeight = "height:" + String(window.innerHeight - 280) + "px";

        $("#dataTableContent").children().attr("style", dataTableHeight);
        $(".v-data-table-header>").prepend("<th style='width:15px'><input type='checkbox' style='width:20px;height:20px' id='allCheck' name='allCheck'></th>")

        this.targetAllCheck = document.querySelector('#allCheck');
        this.targetAllCheck.addEventListener('change', this.setAllCheck);

        this.getDataInit();
        
        $("#tabBar>div>div>div>div").next().css({'background-color':'#6587CD' });
        $("#tabBar>div>div>div>div").next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").css({'margin-right':'8px' });
        $("#tabBar>div>div>div>div").css({'color':'#FFFFFF' });
    },
    updated() {
        $("#report-payment-list").removeClass("bg-active");

        let UrlSplit = window.location.href.split("/");
        let leftPosition = UrlSplit[UrlSplit.length-1];
        $("#" + leftPosition).addClass("bg-active");

        $(".v-data-table-header>tr>th").css({'background-color':'#1BB5F2' });
        $(".v-data-table-header>tr>th").css({'text-align':'center' });
        $(".v-data-table-header>tr>th").css({'font-size':'15px' });
        $(".v-data-table-header>tr>th").css({'font-style':'bold' });
    },        
}
</script>